import { templates } from "./templates.js";
import { selectedImage } from "./taskpane.js";

export function updatePreview() {
  const templateKey = document.getElementById('templateSelect').value;
  const template = templates[templateKey];
  if (!template) return;

  const values = {};
  template.variables.forEach(v => {
    if (v.type === 'file') {
      values[v.name] = selectedImage || '';
    } else {
      values[v.name] = document.getElementById(v.name)?.value || `{{${v.name}}}`;
    }
  });

  const emailBody = template.body(values);
  const iframe = document.getElementById('previewFrame');
  iframe.contentDocument.open();
  iframe.contentDocument.write(emailBody);
  iframe.contentDocument.close();
}