import { templates } from "./templates.js";
import { updatePreview } from "./preview.js";

export function renderFormFields(templateKey) {
  const formFieldsDiv = document.getElementById('formFields');
  const pasteButton = document.getElementById('pasteButton');
  const livePreviewContainer = document.getElementById('livePreviewContainer');

  // Ensure transition styles are set
  [formFieldsDiv, pasteButton, livePreviewContainer].forEach(el => {
    el.style.transition = 'opacity 0.5s ease';
  });

  // Fade out everything first
  formFieldsDiv.style.opacity = 0;
  pasteButton.style.opacity = 0;
  livePreviewContainer.style.opacity = 0;

  // After fade-out completes, clear and rebuild
  setTimeout(() => {
    formFieldsDiv.innerHTML = '';

    const variables = templates[templateKey].variables;

    // Reset parent container opacity so children can fade in
    formFieldsDiv.style.opacity = 1;

    variables.forEach((v, i) => {
      const wrapper = document.createElement('div');

      if (v.type === 'file') {
        const imageUploadContainer = document.createElement('div');
        imageUploadContainer.id = 'imageUploadContainer';
        const imageUploadLabel = document.createElement('label');
        imageUploadLabel.innerText = 'Key Fob Image';
        imageUploadLabel.htmlFor = 'imageUpload';
        const imageUpload = document.createElement('input');
        imageUpload.type = 'file';
        imageUpload.id = 'imageUpload';
        imageUpload.accept = 'image/*';
        
        imageUpload.addEventListener('change', (event) => {
          const file = event.target.files[0];
          if (file) {
            const reader = new FileReader();
            reader.onload = (e) => {
              const img = new Image();
              img.onload = () => {
                if (typeof cv === 'undefined') {
                  console.error('OpenCV.js is not loaded.');
                  return;
                }

                const mat = cv.imread(img);
                const dst = new cv.Mat();
                const dsize = new cv.Size(0, 0);
                const maxDim = 600;
                let width = mat.cols;
                let height = mat.rows;

                if (width > height) {
                  height = Math.round(height * maxDim / width);
                  width = maxDim;
                } else {
                  width = Math.round(width * maxDim / height);
                  height = maxDim;
                }

                cv.resize(mat, dst, new cv.Size(width, height), 0, 0, cv.INTER_AREA);
                const canvas = document.createElement('canvas');
                cv.imshow(canvas, dst);
                
                window.selectedImage = canvas.toDataURL('image/jpeg', 0.8);

                mat.delete();
                dst.delete();

                updatePreview();
              };
              img.src = e.target.result;
            };
            reader.readAsDataURL(file);
          }
        });

        imageUploadContainer.appendChild(imageUploadLabel);
        imageUploadContainer.appendChild(imageUpload);
        wrapper.appendChild(imageUploadContainer);
      } else {
        const label = document.createElement('label');
        label.innerText = camelToTitle(v.name);
        label.htmlFor = v.name;

        const input = document.createElement('input');
        input.type = v.type;
        input.id = v.name;
        input.placeholder = v.name;
        input.addEventListener('input', updatePreview);
        
        wrapper.appendChild(label);
        wrapper.appendChild(input);
      }

      // Initial fade style
      wrapper.style.opacity = 0;
      wrapper.style.transition = 'opacity 0.5s ease';

      formFieldsDiv.appendChild(wrapper);

      // Trigger fade-in staggered
      setTimeout(() => {
        wrapper.style.opacity = 1;
      }, i * 150);
    });

    // After all fields start fading in, fade in pasteButton and livePreviewContainer
    const totalDelay = variables.length * 150;

    setTimeout(() => {
      pasteButton.style.opacity = 1;

      setTimeout(() => {
        livePreviewContainer.style.opacity = 1;
      }, 150);
    }, totalDelay);

  }, 500); // wait for fade-out duration before clearing
}

function camelToTitle(str) {
  return str
    .replace(/([A-Z])/g, " $1") // insert space before capital letters
    .replace(/^./, (char) => char.toUpperCase()); // capitalize the first character
}