export const logoBase64 = "data:image/png;base64,..."; // Replace with actual base64 logo

function baseTemplate(content, v) {
  return `<!doctype html>
<html lang="en">
  <head>
    <meta charset="utf-8">
    <title>${v.documentName}</title>
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
  </head>
  <body style="margin: 0; padding: 24px 0; background-color: #f4f6f8;">
    <div style="display: none; max-height: 0; overflow: hidden; font-size: 1px; line-height: 1px; color: #f4f6f8;">
      ${v.documentShortText || ''}
    </div>
    <table role="presentation" width="650" cellpadding="0" cellspacing="0" border="0" align="center" style="width: 650px; background-color: #ffffff; border-radius: 12px; overflow: hidden; box-shadow: 0 2px 8px rgba(0,0,0,0.06);">
      <tr>
        <td style="background-color: #0c233f; padding: 18px 24px;">
          <table role="presentation" width="100%" cellpadding="0" cellspacing="0">
            <tr>
              <td style="color: #ffffff; font-family: Arial, Helvetica, sans-serif; font-size: 18px; font-weight: 700; line-height: 1.2; padding-bottom: 0px;" align="left" valign="middle">
                <img src="https://i.imgur.com/4Bfswoj.png" alt="EMB Management" height="40" style="display: block; border: 0; max-width: 180px; height: auto;">
              </td>
              <td align="right" valign="middle" style="color: #cfe3fb; font-family: Arial, Helvetica, sans-serif; font-size: 12px; font-weight: 400; line-height: 1.4;">
                ${v.documentName || ''}
              </td>
            </tr>
          </table>
        </td>
      </tr>
      <tr>
        <td style="padding: 30px 40px; color: #333333; font-family: Arial, sans-serif; font-size: 15px; line-height: 1.6;">
          ${content}
        </td>
      </tr>
    </table>
    <table role="presentation" width="100%" cellpadding="0" cellspacing="0" border="0">
      <tr>
        <td align="center" style="padding: 24px 0 0 0;">
          <table role="presentation" width="650" cellpadding="0" cellspacing="0" border="0" align="center" style="width: 650px;">
            <tr>
              <td style="font-family: Arial, sans-serif; font-size: 12px; color: #777777; padding: 10px 40px; text-align: center;">
                <hr style="border: none; border-top: 1px solid #e6edf5; margin: 0 0 12px 0;">
                <p style="margin: 0;">This email may contain confidential information intended only for the recipient. If you are not the intended recipient, please delete this message.</p>
              </td>
            </tr>
          </table>
        </td>
      </tr>
    </table>
  </body>
</html>`;
}

export const templates = {
  payment: {
    name: "Payment Options",
    subject: v => `Payment Option Information for ${v.associationName}`,
    documentName: "Payment Option Information",
    documentShortText: v => `Payment option information for ${v.associationName}, account ${v.accountNumber}`,
    variables: [
      { name: "propertyName", type: "text" },
      { name: "ownerName", type: "text" },
      { name: "associationName", type: "text" },
      { name: "accountNumber", type: "text" },
      { name: "amountDue", type: "text" }
    ],
    body: v => baseTemplate(`
        <h2 style="font-family: Arial, sans-serif; margin-top:0;">Account Balance Information</h2>
        <p>Dear ${v.ownerName},</p>
        <p>Below are the payment options available for <strong>${v.associationName}</strong>. Please be sure to reference your account number <strong>${v.accountNumber}</strong> with all payments.</p>

        <div style="font-size: 16px; font-weight: bold; margin: 20px 0; padding: 15px; background-color: #f8fafc; border-left: 4px solid #0b5cab; color: #333;">
          Current Amount Due: $${v.amountDue}
        </div>

        <div style="border-top: 1px solid #e6edf5; margin: 30px 0;"></div>
        <h2 style="font-family: Arial, sans-serif;"><img src="https://img.icons8.com/?size=24&id=18635&format=png&color=000000" alt="Mail" style="vertical-align: middle; margin-right: 8px;"/> Mailing Payments</h2>
        <p>Please ensure checks are made payable to <strong>${v.associationName}</strong>. Be sure to include your account number on the memo/notes line. Any payment received without an account number may be returned as “Unable to Locate Account.”</p>
        <p>Mail payments to:</p>
        <div style="border:1px solid #0c233f; padding:10px 20px; display:inline-block; margin:10px 0;">
          ${v.associationName}<br>c/o EMB Management<br>PO Box 45450<br>San Francisco, CA 94145-0450
        </div>

        <div style="border-top: 1px solid #e6edf5; margin: 30px 0;"></div>
        <h2 style="font-family: Arial, sans-serif;"><img src="https://img.icons8.com/?size=24&id=6Rw34qscHREa&format=png&color=000000" alt="ACH" style="vertical-align: middle; margin-right: 8px;"/> Automatic Debit (ACH/DD)</h2>
        <p>With ACH, your monthly assessment is automatically drafted from your bank account between the 5th–10th each month. This option is <strong>free of charge</strong> and ensures you never miss a payment or risk late fees.</p>
        <p><a href="https://drive.google.com/file/d/1oEAdpHZpMgn_Dfh2cCc43qALZQsX5o8W/view?usp=sharing" class="button" target="_blank" style="background-color: #0b5cab; color: #ffffff; padding: 10px 15px; text-decoration: none; border-radius: 5px;">Download ACH Form</a></p>
        <p>Please complete the form and return it with a voided check. For activation by next month’s assessment, it must be received by the 20th of the current month. ACH will only pull the current monthly assessment amount due. If your account has a balance beyond the monthly assessment, you must make a one-time payment using another option.</p>

        <div style="border-top: 1px solid #e6edf5; margin: 30px 0;"></div>
        <h2 style="font-family: Arial, sans-serif;"><img src="https://img.icons8.com/?size=24&id=VbL8v3mm1qyp&format=png&color=000000" alt="Bill Pay" style="vertical-align: middle; margin-right: 8px;"/> Bill Pay</h2>
        <p>Bill Pay can be set up through your financial institution or online at <a href="https://app.townsq.io/login">TownSq.io</a>. Please allow 5–7 business days for processing. Make checks payable to <strong>${v.associationName}</strong>, and include your last name/unit number (if applicable) and your account number on the memo line.</p>
        <p>Mail to the same address listed above.</p>

        <div style="border-top: 1px solid #e6edf5; margin: 30px 0;"></div>
        <h2 style="font-family: Arial, sans-serif;"><img src="https://cdn.prod.website-files.com/67c157877f705a5a2a3dcb3c/682b757bf89cef02a37a43d9_TSQ%20Icon_Webclip.png" style="width:18px;padding:4px; vertical-align: middle; margin-right: 8px;" alt="Online"/> Online Payments (TownSq)</h2>
        <p>One-time or recurring payments may be made through <a href="https://app.townsq.io/registration/account-number">TownSq.io</a> using a credit/debit card or checking/savings account. Registration is free.</p>
        <p><strong>How to Register:</strong></p>
        <ul>
          <li style="line-height: 1.6;">Click “Need to Register” on the login page</li>
          <li style="line-height: 1.6;">Enter the property’s zip code</li>
          <li style="line-height: 1.6;">Enter your account number (${v.accountNumber}) — no dashes or leading zeros</li>
          <li style="line-height: 1.6;">Review the terms of service and click "Next".</li>
          <li style="line-height: 1.6;">Verify your acount details are correct and click "Next".</li>
          <li style="line-height: 1.6;">Finally, create a password for your account to finish registration.</li>
        </ul>
        <p><strong>TownSq Fees:</strong></p>
        <ul>
          <li style="line-height: 1.6;">Bank Account (ACH): $2.95 per transaction</li>
          <li style="line-height: 1.6;">Credit/Debit Card: $2.95 + 3.5% of total</li>
        </ul>
        <p>Note: When using a bank account, the 3.5% fee will appear but then show a “cash discount” credit. The 3.5% will be waived in the final total. These fees are charged by TownSq (a third-party processor) and do not go to EMB Management. Using TownSq also ensures your payment information is processed securely.</p>

        <div style="border-top: 1px solid #e6edf5; margin: 30px 0;"></div>
        <h2 style="font-family: Arial, sans-serif;">Need Assistance?</h2>
        <p>We’re here to help. Contact EMB Management at <a href="tel:4254527330">(425) 452-7330</a> or reply to this email with any questions.</p>

        <p>Thank you,<br><br>
        <strong>EMB Management Team</strong><br>
        <a href="https://www.google.com/maps/dir/?api=1&destination=Associa%20EMB%20Management">
        15301 NE 90th Street, Suite Q-170<br>
        Redmond, WA 98052</a>`, v)
  },

  orderingResaleDocs: {
    name: "Ordering Resale Documents",
    subject: v => `Instructions for Ordering Resale Documents for ${v.associationName}`,
    documentName: "Ordering Resale Documents",
    documentShortText: v => `Instructions on how to order resale documents for ${v.associationName}.`,
    variables: [
      { name: "recipientName", type: "text" },
      { name: "associationName", type: "text" }
    ],
    body: v => baseTemplate(`
      <p style="margin-top:0;">Hello ${v.recipientName},</p>
      <p>Thank you for your recent request for the HOA documents for <strong>${v.associationName}</strong>. Resale certificates and questionnaires are handled through our Community Archives department.</p>
      <p>Please follow the steps below to order the necessary documents.</p>

      <div style="border-top: 1px solid #e6edf5; margin: 30px 0;"></div>

      <h2 style="font-family: Arial, sans-serif; margin-top:0;">Step-by-Step Ordering Guide</h2>

      <table role="presentation" width="100%" cellpadding="0" cellspacing="0" border="0" style="font-size: 15px;">
        <tr>
          <td width="40" style="vertical-align: top; font-size: 30px; font-weight: bold; color: #0b5cab; padding-right: 15px;">1.</td>
          <td style="vertical-align: top;">
            <p style="margin-top: 10px; margin-bottom: 20px;">
              Go to the EMB Management website at <a href="https://www.embmanagement.com" target="_blank" style="color:#0b5cab; text-decoration:none;">www.embmanagement.com</a>. Click on the <strong>“Homeowner Resources”</strong> tab, and then click <strong>“Order Resale Documents”</strong>.
            </p>
            <img src="https://i.imgur.com/ifeECdx.png" alt="Navigate to Order Resale Documents" width="550" style="max-width: 550px; height: auto; border: 1px solid #e6edf5; border-radius: 8px;">
          </td>
        </tr>
        <tr><td colspan="2" style="height: 20px;"></td></tr>
        <tr>
          <td width="40" style="vertical-align: top; font-size: 30px; font-weight: bold; color: #0b5cab; padding-right: 15px;">2.</td>
          <td style="vertical-align: top;">
            <p style="margin-top: 10px; margin-bottom: 20px;">
              You will be connected to the Community Archives page. You will need to create an account. Registration is free. Enter your first name, last name, email, and a password, then click "Sign Up". You will be sent an email with a confirmation code to complete your registration.
            </p>
            <img src="https://i.imgur.com/TYpejIz.png" alt="Community Archives Registration" width="300" style="max-width: 300px; height: auto; border: 1px solid #e6edf5; border-radius: 8px;">
          </td>
        </tr>
        <tr><td colspan="2" style="height: 20px;"></td></tr>
        <tr>
          <td width="40" style="vertical-align: top; font-size: 30px; font-weight: bold; color: #0b5cab; padding-right: 15px;">3.</td>
          <td style="vertical-align: top;">
            <p style="margin-top: 10px; margin-bottom: 20px;">
              Once registered, you can find your community by typing the first three letters of the community name (<strong>${v.associationName}</strong>) in the search box and waiting for the name to appear. Select your community from the list.
              <br><strong style="color: #c0392b;">Note:</strong> Do not press the Enter or Space key after typing; wait for the information to populate.
            </p>
          </td>
        </tr>
        <tr><td colspan="2" style="height: 20px;"></td></tr>
        <tr>
          <td width="40" style="vertical-align: top; font-size: 30px; font-weight: bold; color: #0b5cab; padding-right: 15px;">4.</td>
          <td style="vertical-align: top;">
            <p style="margin-top: 10px; margin-bottom: 20px;">
              A list of available documents and products will appear. Select the items you need to purchase to complete your order.
            </p>
          </td>
        </tr>
      </table>

      <div style="border-top: 1px solid #e6edf5; margin: 30px 0;"></div>

      <h3 style="font-family: Arial, sans-serif;">Frequently Asked Questions</h3>
      <p style="margin-bottom: 5px;"><strong>What is the order turnaround time?</strong></p>
      <p style="margin-top:0;">Standard processing time is 5-10 business days. If you need your order sooner, please choose the expedite option, which has a 9-32 business hour processing time.</p>

      <p style="margin-bottom: 5px;"><strong>What if I'm ordering a custom/lender-specific questionnaire?</strong></p>
      <p style="margin-top:0;">Please choose the "Lender/Mortgage Questionnaire" and follow the instructions to order a "Custom Questionnaire." Send the custom questionnaire to <a href="mailto:Customforms@communityarchives.com" style="color:#0b5cab; text-decoration:none;">Customforms@communityarchives.com</a> and include your order number.</p>
      
      <div style="margin: 25px 0; padding: 20px; background-color: #f8fafc; border-radius: 8px; border: 1px solid #e6edf5;">
        <h4 style="margin-top:0; font-family: Arial, sans-serif;">Need Help?</h4>
        <p style="margin-bottom:0;">If you need assistance with navigating the website, registering, or have questions about costs, current orders, or order status, please contact the Community Archives Customer Service department directly.</p>
        <p style="margin-top:10px;">
          <strong>Email:</strong> <a href="mailto:customerservice@communityarchives.com" style="color:#0b5cab; text-decoration:none;">customerservice@communityarchives.com</a><br>
          <strong>Phone:</strong> <a href="tel:1-833-462-3627" style="color:#0b5cab; text-decoration:none;">1-833-462-3627</a>
        </p>
      </div>
    `, v)
  },

  receipt: {
    name: "Confirmed Receipt",
    subject: v => `Payment Receipt Confirmation for ${v.associationName}`,
    documentName: "Payment Receipt",
    documentShortText: v => `Receipt of $${v.amountPaid} from ${v.ownerName} for ${v.associationName}`,
    variables: [
      { name: "propertyName", type: "text" },
      { name: "ownerName", type: "text" },
      { name: "associationName", type: "text" },
      { name: "amountPaid", type: "text" },
      { name: "paymentDate", type: "date" }
    ],
    body: v => baseTemplate(`
      <h2 style="font-family: Arial, sans-serif;">Payment Confirmation</h2>
      <p>Dear ${v.ownerName},</p>
      <p>This is to confirm we received your payment of <strong>$${v.amountPaid}</strong> 
      for <strong>${v.associationName}</strong> on <strong>${v.paymentDate}</strong>.</p>
      <p>Thank you for your timely payment.</p>
    `, v)
  },

  resale: {
    name: "Resale Document Signature",
    subject: v => `Resale Document Signature Request for ${v.associationName}`,
    documentName: "Resale Document Request",
    documentShortText: v => `Signature request for ${v.ownerName} at ${v.propertyAddress}`,
    variables: [
      { name: "propertyName", type: "text" },
      { name: "ownerName", type: "text" },
      { name: "associationName", type: "text" },
      { name: "propertyAddress", type: "text" },
      { name: "deadlineDate", type: "date" }
    ],
    body: v => baseTemplate(`
      <h2 style="font-family: Arial, sans-serif;">Resale Document Signature Required</h2>
      <p>Dear ${v.ownerName},</p>
      <p>To proceed with the sale of <strong>${v.propertyAddress}</strong> in 
      <strong>${v.associationName}</strong>, please review and sign the attached resale documents by 
      <strong>${v.deadlineDate}</strong>.</p>
      <p>If you have questions, contact our office immediately.</p>
    `, v)
  },

  welcome: {
    name: "Welcome Packet",
    subject: v => `Welcome to ${v.associationName}!`,
    documentName: "Welcome Packet",
    documentShortText: v => `Welcome letter for ${v.ownerName} moving into ${v.associationName}`,
    variables: [
      { name: "propertyName", type: "text" },
      { name: "ownerName", type: "text" },
      { name: "associationName", type: "text" },
      { name: "moveInDate", type: "date" }
    ],
    body: v => baseTemplate(`
      <h2 style="font-family: Arial, sans-serif;">Welcome to ${v.associationName}</h2>
      <p>Dear ${v.ownerName},</p>
      <p>Welcome! We’re excited to have you as part of our community starting <strong>${v.moveInDate}</strong>.</p>
      <p>Your welcome packet is attached, which contains important information about community rules, amenities, and contacts.</p>
    `, v)
  },

  keyFob: {
    name: "Key Fob Mailing",
    subject: v => `Key Fob Mailing Confirmation for ${v.associationName} Unit ${v.unitNumber}`,
    documentName: `Key Fob Mailing Confirmation`,
    documentShortText: v => `Confirmation that a new key fob has been mailed to ${v.residentName} at ${v.associationName} Unit ${v.unitNumber} on ${v.mailDate}`,
    variables: [
      { name: "associationName", type: "text" },
      { name: "unitNumber", type: "text" },
      { name: "mailDate", type: "date" },
      { name: "residentName", type: "text" },
      { name: "keyFobId", type: "text" },
      { name: "keyFobImage", type: "file" }
    ],
    body: v => baseTemplate(`
        <p style="margin:0 0 14px 0;">Hello Board Members,</p>

        <p style="margin:0 0 14px 0;">
          This is a confirmation that a new key fob has been mailed to a resident at
          <strong>${v.associationName}</strong>, unit <strong>${v.unitNumber}</strong>, on <strong>${v.mailDate}</strong>.
        </p>

        <table role="presentation" cellpadding="0" cellspacing="0" width="100%" style="margin:16px 0; background:#f8fafc; border:1px solid #e6edf5; border-radius:10px;">
          <tr>
            <td style="padding:16px 18px;">
              <table role="presentation" width="100%" cellpadding="0" cellspacing="0">
                <tr>
                  <td class="stack" style="vertical-align:top; width:60%;">
                    <p style="margin:0 0 8px 0; font:700 14px Arial, Helvetica, sans-serif; color:#0b5cab;">
                      Fulfillment Details
                    </p>
                    <p style="margin:0; font:400 14px Arial, Helvetica, sans-serif;">
                      Resident, <strong>${v.residentName}</strong><br>
                      Unit, <strong>${v.unitNumber}</strong><br>
                      Key Fob ID, <strong style="letter-spacing:0.5px;">${v.keyFobId}</strong><br>
                      Mail date, <strong>${v.mailDate}</strong>
                    </p>
                  </td>
                  <td class="stack" style="vertical-align:top; width:40%; text-align:right;">
                    <img src="${v.keyFobImage}"
                         width="220"
                         alt="Back of key fob showing ID ${v.keyFobId}"
                         style="max-width:220px; height:auto; border-radius:8px; border:1px solid #e6edf5; display:inline-block;">
                    <div style="font:12px Arial, Helvetica, sans-serif; color:#5b6b7a; margin-top:6px;">
                      Image, back of key fob for verification
                    </div>
                  </td>
                </tr>
              </table>
            </td>
          </tr>
        </table>

        <p style="margin:0 0 12px 0;">
          Action requested, please activate the key fob with ID <strong>${v.keyFobId}</strong> for unit <strong>${v.unitNumber}</strong>.
        </p>

        <p style="margin:0 0 18px 0; color:#5b6b7a;">
          If the ID on the image does not match the value above, reply to this email with the corrected ID so we can update our records immediately.
        </p>

        <p style="margin:0 0 6px 0;">Thank you,</p>
        <p style="margin:0;">
          <strong>Lena Papakonstantinou</strong><br>
          Receptionist, EMB Management<br>
          <span style="color:#5b6b7a;">425-452-7330</span>  |  <a href="mailto:lpapakonstantinou@embmanagement.com" style="color:#0b5cab; text-decoration:none;">lpapakonstantinou@embmanagement.com</a>
        </p>
      </td>
    </tr>

    <tr>
      <td style="padding:18px 28px 28px 28px; font:12px Arial, Helvetica, sans-serif; color:#6b7a89;">
        <hr style="border:none; border-top:1px solid #e6edf5; margin:0 0 12px 0;">
        <p style="margin:0;">
          This message is intended for the board of <strong>${v.associationName}</strong>. If you received it in error, please let us know, then delete it.
        </p>
    `, v)
  }
};